.header {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 10;
  justify-content: flex-start;
}

.header__wrapper {
  width: 100%;
  padding: 18px 0;
}

.header__wrapper-black {
  background: #f8f8f8;
}

.header__wrapper-black .header__nav a {
  color: #000000;
}

.header__wrapper-white {
  background: transparent;
}

.header__wrapper-white .header__nav a {
  color: #ffffff;
}

.header__wrapper .containers {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.header__navs {
  width: max-content;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 25px;
}

.header__nav {
  cursor: pointer;
  position: relative;
}

.header__nav-a {
  position: relative;
}

.header__nav-a::after {
  opacity: 1 !important;
}

.header__nav:hover::after {
  opacity: 1;
}

.header__nav::after {
  position: absolute;
  content: "";
  bottom: -10px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 90%;
  height: 4px;
  background: #ff8743;
  transition: all 0.5s ease;
  opacity: 0;
  border-radius: 10px;
}

.header__nav a {
  font-family: "NunitoSans";
  font-style: normal;
  font-weight: 700;
  font-size: 13px;
  line-height: 18px;
}

.header__banner {
  width: 100%;
  min-height: 52px;
  background: #d5ba64;
  padding: 11px 0;
}

.header__banner .containers {
